.myModal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 100;
    
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(10px);

    display: none;
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}